<template>
  <div>
    <el-button type="primary" @click="reformatDateMethods"
      >执行日期函数</el-button
    >
  </div>
</template>

<script>
export default {
  props: {},
  components: {},
  computed: {},
  data() {
    return {};
  },
  async mounted() {},
  methods: {
    reformatDateMethods() {
      const dates = [
        "2024年1月1日",
        "2024年1月2日",
        "2024年1月3日",
        "2024年1月4日",
        "2024年1月5日",
        "2024年1月6日",
        "2024年1月7日",
        "2024年1月8日",
      ];
      console.log(this.formatDates(dates)); //
      let arr1 = this.formatDates(dates);
      let arr2 = [];
      for (let i in arr1) {
        let a = this.splitDates(arr1[i]);
        arr2.push(a);
      }
      console.log('arr2 :>> ', arr2);
    },
    splitDates(dateStrings) {
      if (!dateStrings || dateStrings.length === 0) {
        return "";
      }

      let formattedDates = "";

      for (let i = 0; i < dateStrings.length; i++) {
        const dateString = dateStrings[i];

        if (i === 0) {
          formattedDates += dateString;
        } else {
          const [, , day] = dateString.match(/\d+/g).map(Number);
          formattedDates += `、${day}日`;
        }
      }

      return formattedDates;
    },
    formatDates(dateStrings) {
      if (!dateStrings || dateStrings.length === 0) {
        return [];
      }

      const dates = dateStrings.map((dateString) => {
        const [year, month, day] = dateString.match(/\d+/g).map(Number);
        return { year, month, day };
      });

      const result = [];
      let currentConsecutive = [];

      for (let i = 0; i < dates.length; i++) {
        if (currentConsecutive.length === 0) {
          currentConsecutive.push(dates[i]);
        } else {
          const lastDate = currentConsecutive[currentConsecutive.length - 1];
          const currentDate = dates[i];

          if (
            lastDate.year !== currentDate.year ||
            lastDate.month !== currentDate.month ||
            lastDate.day !== currentDate.day - 1
          ) {
            result.push(
              currentConsecutive.map(
                (date) => `${date.year}年${date.month}月${date.day}日`
              )
            );
            currentConsecutive = [currentDate];
          } else {
            currentConsecutive.push(currentDate);
          }
        }
      }

      if (currentConsecutive.length > 0) {
        result.push(
          currentConsecutive.map(
            (date) => `${date.year}年${date.month}月${date.day}日`
          )
        );
      }

      return result;
    },
  },
};
</script>

<style lang="scss" scoped></style>
